* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.switcher {
  height: 500px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button {
  width: 140px;
  height: 40px;
  text-transform: uppercase;
  color: #f0f8ff;
  cursor: pointer;
  background-color: #116f94;
  border: none;
  border-radius: 10px;
  font-size: 25px;
  position: relative;
  bottom: -3px;
  box-shadow: 3px 3px 2px #2d2b2b;
}

.button--rigth-indent {
  margin-right: 20px;
}

.button:focus, .button:hover {
  background-color: #2ea0cd;
}

.button:active {
  box-shadow: none;
  bottom: -6px;
  right: -3px;
}

.container {
  width: 1030px;
  text-align: center;
  margin: 0 auto;
  padding: 100px 15px;
}

.wrapper {
  width: 500px;
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
}

#datetime-picker {
  height: 40px;
  width: 600px;
  border-radius: 10px;
}

#datetime-picker:focus {
  border: 2px solid #019101;
  outline: none;
}

.timer {
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
}

.field {
  min-width: 140px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.field:not(:last-child) {
  margin-right: 35px;
}

.value {
  color: #019101;
  font-family: PT Mono, monospace;
  font-size: 92px;
  line-height: 1;
}

.label {
  font-size: 35px;
}

.buttons {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.timer-button {
  width: 180px;
  height: 180px;
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(255, 21, 0, .867);
  border: none;
  border-radius: 50%;
  padding: 0;
  font-size: 40px;
  position: relative;
  box-shadow: 5px 5px 2px #2c2a2a;
}

.timer-button:focus, .timer-button:hover {
  background-color: #ff2f00;
}

.timer-button:active {
  box-shadow: none;
  bottom: -3px;
  right: -3px;
}

.timer-button--oposide-color {
  background-color: #007bff;
}

.timer-button--oposide-color:focus, .timer-button--oposide-color:hover {
  background-color: #1772d4;
}

